@font-face {
  font-family: 'Raleway';
  src: url('/assets/fonts/Raleway-SemiBold.ttf') format('truetype'); }

@font-face {
  font-family: 'Oswald';
  src: url('/assets/fonts/Oswald-Bold.ttf') format('truetype'); }

$default-background: "/assets/image/3rd_background.jpg";
$primary-font: 'Oswald', sans-serif;
$secondary-font: 'Raleway', sans-serif;
$navbar-height: 50px;
$link-color: rgba(170, 64, 0, 1);

body {
  background: rgba(255,255,255,1);

  .container {
    min-height: 100vh;
    height: 100%;
    width: 80%;

    h1, h2, h3, h4, h5, h6 {
      font-family: $primary-font; }

    .content {
      font-size: 1.3rem; }

    .content {
      min-height: 100%; }

    .heading {
      font-family: $primary-font; }

    p {
      font-family: $secondary-font; }

    a {
      color: black;
      transition: color 700ms ease;

      &:hover {
        color: darkviolet;
        text-decoration: none; }

      & .animated:hover {
        -webkit-animation: tracking-in-contract 0.3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
        animation: tracking-in-contract 0.3s cubic-bezier(0.215, 0.610, 0.355, 1.000) both; } }

    .avatarImage {
      height: 200px;
      width: 200px; } } }

@-webkit-keyframes tracking-in-contract {
  0% {}
  letter-spacing: 1em;
  opacity: 0;
  40% {}
  opacity: 0.6;
  100% {}
  letter-spacing: normal;
  opacity: 1; }

@keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0; }
  40% {
    opacity: 0.6; }
  100% {
    letter-spacing: normal;
    opacity: 1; } }
